/* Base for label styling */
.datapadRow [type="checkbox"]:not(:checked),
.datapadRow [type="checkbox"]:checked {
  position: absolute;
  left: -9999px; }

.datapadRow [type="checkbox"]:not(:checked) + label,
.datapadRow [type="checkbox"]:checked + label {
  padding-left: 1.95em;
  cursor: pointer; }

/* checkbox aspect */
.datapadRow [type="checkbox"]:not(:checked) + label:before,
.datapadRow [type="checkbox"]:checked + label:before {
  content: '';
  position: absolute;
  left: -2px;
  top: -2px;
  width: 1.6em;
  height: 1.6em;
  /*border: 2px solid #ccc;*/
  background: #fff;
  border-radius: 4px;
  /*box-shadow: inset 0 1px 3px rgba(0,0,0,.1);*/ }

/* checked mark aspect */
.datapadRow [type="checkbox"]:not(:checked) + label:after,
.datapadRow [type="checkbox"]:checked + label:after {
  /*content: '\2713\0020';*/
  content: '';
  width: 1.6em;
  height: 1.6em;
  position: absolute;
  left: -2px;
  top: -2px;
  background: url("data:image/svg+xml;base64,") !important;
  background-size: 100% !important;
  background-position: cover;
  /*font-size: 1.3em;
  line-height: 0.8;
  color: #09ad7e;*/
  transition: all .2s;
  font-family: 'Lucida Sans Unicode', 'Arial Unicode MS', Arial; }

/* checked mark aspect changes */
.datapadRow [type="checkbox"]:not(:checked) + label:after {
  opacity: 0;
  transform: scale(0); }

.datapadRow [type="checkbox"]:checked + label:after {
  opacity: 1;
  transform: scale(1); }

/* disabled checkbox */
.datapadRow [type="checkbox"]:disabled:not(:checked) + label:before,
.datapadRow [type="checkbox"]:disabled:checked + label:before {
  box-shadow: none;
  border-color: #bbb;
  background-color: #ddd; }

.datapadRow [type="checkbox"]:disabled:checked + label:after {
  color: #999; }

.datapadRow [type="checkbox"]:disabled + label {
  color: #aaa; }

/* accessibility */
.datapadRow [type="checkbox"]:checked:focus + label:before,
.datapadRow [type="checkbox"]:not(:checked):focus + label:before {
  border: 2px dotted blue; }

.datapadRow label {
  position: absolute;
  margin-left: -50px;
  z-index: 99; }

/* hover style just for information */
label:hover:before {
  /*border: 2px solid #4778d9!important;*/
  background: url("data:image/svg+xml;base64,") !important; }

